'use client';

import React, { useRef, useImperativeHandle, forwardRef, useEffect } from 'react';
import { ImageIcon } from 'lucide-react';
import { Button } from '@/components/ui/button';
import FilePreview from './filepreview';
import { useFileUpload } from '../hooks/UseFileUploads';
import { useMutation } from '@apollo/client';
import { GET_PRESIGNED_URL, COMPLETE_FILE_UPLOAD } from '@/app/apollo/mutations/uploadmutations';
import { useToast } from '@/components/ui/use-toast';

// Define types for the component
interface FileUploaderProps {
  onUploadComplete?: (fileUrls: string[]) => void;
  disabled?: boolean;
  resetTrigger?: number; // Add reset trigger prop
}

// Export type for the ref
export interface FileUploaderRef {
  clearFiles: () => void;
}

export const FileUploader = forwardRef<FileUploaderRef, FileUploaderProps>(({ 
  onUploadComplete, 
  disabled = false,
  resetTrigger = 0
}, ref) => {
  const fileInputRef = useRef<HTMLInputElement>(null);
  const { toast } = useToast();
  
  // GraphQL mutations
  const [getPresignedUrlMutation] = useMutation(GET_PRESIGNED_URL);
  const [completeFileUploadMutation] = useMutation(COMPLETE_FILE_UPLOAD);
  
  // Use file upload hook
  const { 
    files, 
    isUploading, 
    addFiles, 
    removeFile, 
    uploadAllFiles,
    clearFiles // Get the clearFiles method from the hook
  } = useFileUpload({
    getPresignedUrl: getPresignedUrlMutation,
    completeFileUpload: completeFileUploadMutation,
    onError: (error, file) => {
      toast({
        variant: "destructive",
        title: "Upload failed",
        description: `Failed to upload ${file.name}. Please try again.`
      });
    }
  });
  
  // Expose the clearFiles method via ref
  useImperativeHandle(ref, () => ({
    clearFiles: () => {
      clearFiles();
      // Also reset the file input
      if (fileInputRef.current) {
        fileInputRef.current.value = '';
      }
    }
  }), [clearFiles]);
  
  // Effect to clear files when resetTrigger changes
  // Added a check for resetTrigger > 0 to prevent running on initial render
  useEffect(() => {
    // Skip the effect on initial render (when resetTrigger is 0)
    if (resetTrigger > 0 && files.length > 0) {
      clearFiles();
      if (fileInputRef.current) {
        fileInputRef.current.value = '';
      }
    }
  }, [resetTrigger, clearFiles, files.length]);
  
  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFiles = e.target.files;
    if (selectedFiles && selectedFiles.length > 0) {
      addFiles(selectedFiles);
      
      // Reset the input so the same file can be selected again
      e.target.value = '';
    }
  };
  
  const handleUploadClick = async () => {
    try {
      const fileUrls = await uploadAllFiles();
      
      if (onUploadComplete) {
        onUploadComplete(fileUrls);
      }
      
      toast({
        title: "Files uploaded successfully",
        description: `${fileUrls.length} file(s) uploaded`
      });
    } catch (error) {
      console.error('Error uploading files:', error);
      toast({
        variant: "destructive",
        title: "Upload failed",
        description: "An error occurred while uploading files."
      });
    }
  };
  
  return (
    <div className="space-y-4">
      <input
        type="file"
        ref={fileInputRef}
        onChange={handleFileChange}
        multiple
        accept="image/jpeg,image/png,image/gif,image/webp,video/mp4,video/webm,video/quicktime,application/pdf,application/vnd.ms-powerpoint,application/vnd.openxmlformats-officedocument.presentationml.presentation,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document,application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,text/plain,application/zip,application/x-zip-compressed"
        className="hidden"
        disabled={disabled || isUploading}
      />
      
      <Button 
        variant="outline" 
        size="sm" 
        className="gap-1"
        onClick={() => fileInputRef.current?.click()}
        disabled={disabled || isUploading}
      >
        <ImageIcon className="h-4 w-4" /> Select Files
      </Button>
      
      {files.length > 0 && (
        <div className="mt-3">
          <div className="text-sm text-muted-foreground mb-2">
            {files.length} {files.length === 1 ? 'file' : 'files'} selected
          </div>
          <div className="grid grid-cols-2 md:grid-cols-3 gap-2">
            {files.map((file) => (
              <FilePreview 
                key={file.id} 
                file={file} 
                onRemove={removeFile} 
                disabled={disabled || isUploading}
              />
            ))}
          </div>
          
          {files.some(file => file.status === 'ready' || file.status === 'error') && (
            <Button 
              className="mt-4" 
              onClick={handleUploadClick}
              disabled={disabled || isUploading}
            >
              {isUploading ? 'Uploading...' : 'Upload Files'}
            </Button>
          )}
        </div>
      )}
    </div>
  );
});

// Set display name for the component
FileUploader.displayName = 'FileUploader';

export default FileUploader;