import { useState } from 'react';

// Types for file handling
export interface FileWithPreview {
  id: string;
  file: File;
  name: string;
  size: number;
  type: string;
  extension: string;
  preview: string | null;
  progress: number;
  status: 'ready' | 'uploading' | 'success' | 'error';
  fileUrl: string | null;
}

export interface PresignedUrlResponse {
  uploadUrl: string;
  fileUrl: string;
  key: string;
}

export interface FileUploadCompleteResponse {
  success: boolean;
  fileUrl: string;
}

// S3 upload function with progress tracking
export const uploadFileToS3 = async (
  file: File, 
  presignedUrl: string, 
  onProgress?: (progress: number) => void
): Promise<void> => {
  return new Promise((resolve, reject) => {
    const xhr = new XMLHttpRequest();
    
    // Track upload progress
    xhr.upload.onprogress = (event) => {
      if (event.lengthComputable && onProgress) {
        const percentComplete = Math.round((event.loaded / event.total) * 100);
        onProgress(percentComplete);
      }
    };
    
    // Handle success
    xhr.onload = () => {
      if (xhr.status === 200) {
        resolve();
      } else {
        reject(new Error(`Upload failed with status: ${xhr.status}`));
      }
    };
    
    // Handle error
    xhr.onerror = () => {
      reject(new Error('Upload failed due to network error'));
    };
    
    // Start the upload
    xhr.open('PUT', presignedUrl, true);
    xhr.setRequestHeader('Content-Type', file.type);
    xhr.send(file);
  });
};

// Helper functions for file handling
export const isImageFile = (file: File): boolean => {
  return file.type.startsWith('image/');
};

export const isVideoFile = (file: File): boolean => {
  return file.type.startsWith('video/');
};

export const getFileExtension = (filename: string): string => {
  return filename.split('.').pop()?.toLowerCase() || '';
};

export const isImageUrl = (url: string): boolean => {
  const extension = getFileExtension(url);
  return ['jpg', 'jpeg', 'png', 'gif', 'webp', 'svg'].includes(extension);
};

export const isVideoUrl = (url: string): boolean => {
  const extension = getFileExtension(url);
  return ['mp4', 'webm', 'mov', 'avi'].includes(extension);
};

// Determine the appropriate icon based on file extension
export const getFileIconType = (extension: string): string => {
  if (!extension) return 'file';
  
  const ext = extension.toLowerCase();
  
  if (['pdf'].includes(ext)) {
    return 'file-text';
  } else if (['doc', 'docx'].includes(ext)) {
    return 'file-text';
  } else if (['xls', 'xlsx', 'csv'].includes(ext)) {
    return 'file-spreadsheet';
  } else if (['ppt', 'pptx'].includes(ext)) {
    return 'file-presentation';
  } else if (['zip', 'rar', '7z'].includes(ext)) {
    return 'file-archive';
  } else if (['txt', 'rtf'].includes(ext)) {
    return 'file-text';
  }
  
  return 'file';
};