'use client';

import React from 'react';
import { X, CheckCircle, AlertCircle, Loader2, FileIcon } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Progress } from '@/components/ui/progress';
import { FileWithPreview, getFileIconType } from '@/lib/s3client';

interface FilePreviewProps {
  file: FileWithPreview;
  onRemove: (fileId: string) => void;
  disabled?: boolean;
}

export const FilePreview: React.FC<FilePreviewProps> = ({ 
  file, 
  onRemove, 
  disabled = false 
}) => {
  const getStatusIndicator = () => {
    switch (file.status) {
      case 'uploading':
        return (
          <div className="absolute inset-0 bg-black/50 flex flex-col items-center justify-center text-white rounded-md">
            <Loader2 className="h-5 w-5 animate-spin mb-1" />
            <div className="text-xs">{file.progress}%</div>
            <Progress 
              value={file.progress} 
              className="w-3/4 h-1 mt-2" 
            />
          </div>
        );
      case 'success':
        return (
          <div className="absolute top-1 right-1">
            <CheckCircle className="h-5 w-5 text-green-500" />
          </div>
        );
      case 'error':
        return (
          <div className="absolute inset-0 bg-red-500/20 flex items-center justify-center rounded-md">
            <AlertCircle className="h-5 w-5 text-red-500" />
          </div>
        );
      default:
        return null;
    }
  };
  
  const renderFileIcon = () => {
    const iconType = getFileIconType(file.extension);
    return <FileIcon className="h-12 w-12" />;
  };
  
  return (
    <div className="relative group">
      {/* Image preview */}
      {file.type.startsWith('image/') ? (
        <div className="relative rounded-md overflow-hidden h-24">
          <img 
            src={file.preview || ''} 
            alt={`Preview of ${file.name}`} 
            className="w-full h-full object-cover" 
          />
          {getStatusIndicator()}
        </div>
      ) : file.type.startsWith('video/') ? (
        /* Video preview */
        <div className="relative rounded-md overflow-hidden h-24">
          <video 
            src={file.preview || undefined} 
            className="w-full h-full object-cover" 
          />
          <div className="absolute inset-0 flex items-center justify-center bg-black/30">
            <div className="rounded-full bg-white/80 p-1">
              <svg width="16" height="16" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M5 3l14 9-14 9V3z" fill="currentColor" />
              </svg>
            </div>
          </div>
          {getStatusIndicator()}
        </div>
      ) : (
        /* Other file types */
        <div className="flex flex-col items-center justify-center h-24 bg-muted rounded-md p-2">
          {renderFileIcon()}
          <div className="text-xs text-center font-medium mt-1">
            {file.extension}
          </div>
          {getStatusIndicator()}
        </div>
      )}
      
      {/* Remove button (hidden when uploading) */}
      {!disabled && file.status !== 'uploading' && (
        <Button 
          variant="destructive" 
          size="icon" 
          className="absolute top-1 right-1 h-6 w-6 opacity-70 hover:opacity-100" 
          onClick={() => onRemove(file.id)}
        >
          <X className="h-3 w-3" />
        </Button>
      )}
      
      {/* Filename */}
      <div className="absolute bottom-0 left-0 right-0 bg-black/60 text-white text-xs p-1 truncate">
        {file.name.length > 15 ? file.name.substring(0, 12) + '...' : file.name}
      </div>
    </div>
  );
};

export default FilePreview;