'use client';

import { useState, useCallback } from 'react';
import { v4 as uuidv4 } from 'uuid';

// Types for file handling
export interface FileWithPreview {
  id: string;
  file: File;
  name: string;
  size: number;
  type: string;
  extension: string;
  preview: string | null;
  progress: number;
  status: 'ready' | 'uploading' | 'success' | 'error';
  fileUrl: string | null;
}

// Types for GraphQL mutations
interface GetPresignedUrlVariables {
  filename: string;
  contentType: string;
}

interface CompleteFileUploadVariables {
  key: string;
  originalFilename: string;
  fileSize: number;
  fileType: string;
  fileUrl: string;
}

interface UseFileUploadOptions {
  getPresignedUrl: (variables: { variables: GetPresignedUrlVariables }) => Promise<any>;
  completeFileUpload: (variables: { variables: CompleteFileUploadVariables }) => Promise<any>;
  onError?: (error: Error, file: FileWithPreview) => void;
}

export const useFileUpload = ({
  getPresignedUrl,
  completeFileUpload,
  onError
}: UseFileUploadOptions) => {
  const [files, setFiles] = useState<FileWithPreview[]>([]);
  const [isUploading, setIsUploading] = useState<boolean>(false);
  
  // Add files to the queue
  const addFiles = (newFiles: FileList | File[]): FileWithPreview[] => {
    const fileArray = Array.from(newFiles);
    
    const fileObjects = fileArray.map(file => {
      // Determine if we should create a preview
      const shouldCreatePreview = file.type.startsWith('image/') || file.type.startsWith('video/');
      
      return {
        id: uuidv4(),
        file,
        name: file.name,
        size: file.size,
        type: file.type,
        extension: file.name.split('.').pop()?.toUpperCase() || '',
        preview: shouldCreatePreview ? URL.createObjectURL(file) : null,
        progress: 0,
        status: 'ready' as const,
        fileUrl: null
      };
    });
    
    setFiles(prevFiles => [...prevFiles, ...fileObjects]);
    return fileObjects;
  };
  
  // Remove a file
  const removeFile = (fileId: string): void => {
    setFiles(prevFiles => {
      const filesToKeep = prevFiles.filter(file => file.id !== fileId);
      
      // Revoke object URL to avoid memory leaks
      const fileToRemove = prevFiles.find(file => file.id === fileId);
      if (fileToRemove?.preview) {
        URL.revokeObjectURL(fileToRemove.preview);
      }
      
      return filesToKeep;
    });
  };
  
  // Upload a single file
  const uploadFile = async (fileObj: FileWithPreview): Promise<string | null> => {
    try {
      // Update file status
      setFiles(prevFiles => 
        prevFiles.map(f => f.id === fileObj.id 
          ? { ...f, status: 'uploading', progress: 0 } 
          : f
        )
      );
      
      // Get presigned URL
      const { data } = await getPresignedUrl({
        variables: {
          filename: fileObj.name,
          contentType: fileObj.file.type
        }
      });
      
      const { uploadUrl, fileUrl, key } = data.getPresignedUrl;
      
      // Upload to S3
      await uploadFileToS3(fileObj.file, uploadUrl, (progress) => {
        // Update progress
        setFiles(prevFiles => 
          prevFiles.map(f => f.id === fileObj.id 
            ? { ...f, progress } 
            : f
          )
        );
      });
      
      // Complete upload
      await completeFileUpload({
        variables: {
          key,
          originalFilename: fileObj.name,
          fileSize: fileObj.size,
          fileType: fileObj.file.type,
          fileUrl
        }
      });
      
      // Update file status
      setFiles(prevFiles => 
        prevFiles.map(f => f.id === fileObj.id 
          ? { ...f, status: 'success', progress: 100, fileUrl } 
          : f
        )
      );
      
      return fileUrl;
    } catch (error) {
      console.error(`Error uploading file ${fileObj.name}:`, error);
      
      // Update file status to error
      setFiles(prevFiles => 
        prevFiles.map(f => f.id === fileObj.id 
          ? { ...f, status: 'error', progress: 0 } 
          : f
        )
      );
      
      if (onError && error instanceof Error) {
        onError(error, fileObj);
      }
      
      return null;
    }
  };
  
  // Upload all files
  const uploadAllFiles = async (): Promise<string[]> => {
    if (files.length === 0) return [];
    
    setIsUploading(true);
    
    try {
      // Upload files one by one
      const uploadPromises = files
        .filter(file => file.status !== 'success') // Skip already uploaded files
        .map(async (file) => {
          try {
            return await uploadFile(file);
          } catch (error) {
            // If a file fails, we'll continue with others
            return null;
          }
        });
      
      const results = await Promise.all(uploadPromises);
      
      // Collect all successfully uploaded file URLs
      const successfulUploads = results.filter((url): url is string => url !== null);
      
      // Add URLs of previously uploaded files
      const previouslyUploadedUrls = files
        .filter(file => file.status === 'success' && file.fileUrl !== null)
        .map(file => file.fileUrl as string);
      
      return [...successfulUploads, ...previouslyUploadedUrls];
    } catch (error) {
      console.error("Error uploading files:", error);
      throw error;
    } finally {
      setIsUploading(false);
    }
  };
  
  // Clear all files - using useCallback to prevent infinite loops
  const clearFiles = useCallback((): void => {
    // Revoke all object URLs to avoid memory leaks
    files.forEach(file => {
      if (file.preview) {
        URL.revokeObjectURL(file.preview);
      }
    });
    
    // Clear files state
    setFiles([]);
  }, [files]);
  
  // Helper function to upload a file to S3 with progress tracking
  const uploadFileToS3 = (
    file: File, 
    presignedUrl: string, 
    onProgress?: (progress: number) => void
  ): Promise<void> => {
    return new Promise<void>((resolve, reject) => {
      const xhr = new XMLHttpRequest();
      
      // Track upload progress
      xhr.upload.onprogress = (event) => {
        if (event.lengthComputable && onProgress) {
          const percentComplete = Math.round((event.loaded / event.total) * 100);
          onProgress(percentComplete);
        }
      };
      
      // Handle success
      xhr.onload = () => {
        if (xhr.status === 200) {
          resolve();
        } else {
          reject(new Error(`Upload failed with status: ${xhr.status}`));
        }
      };
      
      // Handle error
      xhr.onerror = () => {
        reject(new Error('Upload failed due to network error'));
      };
      
      // Start upload
      xhr.open('PUT', presignedUrl);
      xhr.setRequestHeader('Content-Type', file.type);
      xhr.send(file);
    });
  };
  
  return {
    files,
    isUploading,
    addFiles,
    removeFile,
    uploadFile,
    uploadAllFiles,
    clearFiles
  };
};