'use client';

import React from 'react';
import { FileIcon } from 'lucide-react';
import { isImageUrl, isVideoUrl, getFileExtension, getFileIconType } from '@/lib/s3client';

interface MediaDisplayProps {
  url: string;
  className?: string;
}

export const MediaDisplay: React.FC<MediaDisplayProps> = ({ url, className = '' }) => {
  if (!url) return null;
  
  if (isImageUrl(url)) {
    return (
      <div className={`rounded-md overflow-hidden ${className}`}>
        <img 
          src={url} 
          alt="Media" 
          className="w-full h-auto" 
          loading="lazy"
        />
      </div>
    );
  } 
  
  if (isVideoUrl(url)) {
    return (
      <div className={`rounded-md overflow-hidden ${className}`}>
        <video 
          src={url} 
          controls 
          className="w-full h-auto" 
        />
      </div>
    );
  }
  
  // For other file types, show a download link
  const extension = getFileExtension(url);
  const iconType = getFileIconType(extension);
  
  return (
    <div className={`p-4 border rounded-md flex items-center gap-3 ${className}`}>
      <FileIcon className="h-6 w-6" />
      <div className="flex-1 truncate">
        <a 
          href={url} 
          target="_blank" 
          rel="noopener noreferrer" 
          className="text-sm font-medium hover:underline text-blue-600"
        >
          Download File
        </a>
        <p className="text-xs text-muted-foreground">{extension.toUpperCase()} file</p>
      </div>
    </div>
  );
};

export default MediaDisplay;